  export default {
    name: 'ValueCard',
    components: {
      GiveProducts,
      RuleProducts,
      SelectShop,
      SelectProductType,
    },
    data: function () {
      return {
        modal: {
          show: false,
          title: '',
          type: false,
          width: 800,
          styles: { top: '50px' },
        },
        defaultList: [],
        editor: null,
        editorData: '',
        shopList: [
          {
            id: '1',
            name: '',
          },
          {
            id: '2',
            name: '1',
          },
          {
            id: '3',
            name: '2',
          },
          {
            id: '4',
            name: '3',
          },
          {
            id: '5',
            name: '4',
          },
        ],
        shopPriceList: [
          {
            id: '1',
            name: '',
          },
          {
            id: '2',
            name: '1',
          },
        ],
        valueTypeList: [],
        formData: {
          name: '',
          sids: [],
          prices: {
            total: '',
            shopList: [],
          },
          money: '',
          valueTypeId: '',
          ruleList: [],
          buyGiftList: [],
          renewGiftList: [],
          sort: '',
          info: '',
          status: '',
          limit: '',
        },
        dropdown: {
          show: false,
          title: '',
        },
      }
    },
    computed: {
      modalShow() {
        return (name) => {
          return this.modal.type === name && this.modal.show
        }
      },
    },
    mounted() {
      const editor = new wangEditor(`#editor`)

      //  onchange  vue 
      editor.config.onchange = (newHtml) => {
        this.editorData = newHtml
        this.$emit('editorContent', newHtml)
      }
      // 
      editor.create()
      this.editor = editor
    },
    methods: {
      showModal(title, type, data = {}) {
        console.log(data)
        switch (type) {
          case 'RuleProducts':
            this.modal.width = window.innerWidth - 300
            break
          default:
            this.modal.width = 80
        }
        this.modal.show = true
        this.modal.title = title
        this.modal.type = type
      },
      show(obj) {
        if (
          obj.currentTarget.children[0].className ===
          'ivu-icon ivu-icon-md-arrow-dropright'
        ) {
          obj.currentTarget.children[0].className =
            'ivu-icon ivu-icon-md-arrow-dropdown'
          this.dropdown.title = ''
          this.dropdown.show = true
        } else {
          obj.currentTarget.children[0].className =
            'ivu-icon ivu-icon-md-arrow-dropright'
          this.dropdown.title = ''
          this.dropdown.show = false
        }
      },
      getEditorData() {
        // 
        let data = this.editor.txt.html()
        alert(data)
      },
      beforeDestroy() {
        //  API 
        this.editor.destroy()
        this.editor = null
      },
      removeFile(res) {
        console.log(res, 'removeFile')
        this.formData.img_id = ''
      },
      uploadFile(res) {
        this.formData.img_id = res.id
        console.log(res, 'uploadFile')
      },
      brand() {},
      unit() {},
      productType() {},
      delRuleList() {
        this.formData.ruleList = []
      },
      setRuleList(data) {
        this.formData.ruleList = data
        this.modal.show = false
      },
      delBuyGiftList() {
        this.formData.buyGiftList = []
      },
      setBuyGiftList(data) {
        this.formData.buyGiftList = data
        this.modal.show = false
      },
      delRenewGiveList() {
        this.formData.renewGiftList = []
      },
      setRenewGiftList(data) {
        this.formData.renewGiftList = data
        this.modal.show = false
      },
      showRuleProducts() {
        if (this.shopPriceList.length <= 0) {
          this.$Message.error('')
          return false
        }
        this.showModal('', 'RuleProducts')
      },
    },
  }
</script>

